.card_items {
	display: flex;
	flex-wrap: wrap;
	// justify-content: space-between;

	.card_item {
		width: rem(400);

		margin-right: rem(20);
		margin-bottom: rem(20);

		&:nth-child(3n) {
			margin-right: 0;
		}

		@include min {
			width: calc(50% - 0.625rem);

			&:nth-child(2n) {
				margin-right: 0;
			}

			&:nth-child(3n) {
				margin-right: rem(20);
			}
		}

		@include min768 {
			width: 100%;
			margin-right: 0 !important;
		}
		
		&-top {
			position: relative;
			height: rem(320);
			border-top-right-radius: rem(10);
			border-top-left-radius: rem(10);
			overflow: hidden;

			@include min {
				height: rem(285);
			}

			@include min768 {
				height: rem(210);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-top-right-radius: rem(10);
				border-top-left-radius: rem(10);
			}

			&_sale {
				position: absolute;
				top: rem(-18);
				right: rem(-70);
				width: rem(190);
				height: rem(83);
				padding-bottom: rem(6);
				background-color: #F0F7FF;
				display: flex;
				align-items: flex-end;
				justify-content: center;
				z-index: 2;

				transform: rotate(41.67deg);

				span {
					font-size: rem(14);
					line-height: rem(17);
					color: #05E176;
					text-transform: uppercase;
					font-weight: 700;
					width: 50%;
					text-align: center;
				}
			}
		}

		&-bottom {
			padding: rem(36) rem(26);
			background-color: #F0F7FF;

			@include min768 {
				padding: rem(26) rem(18);
			}

			.title {
				font-size: rem(24);
				font-weight: 600;
				color: #373737;
				display: block;
				margin-bottom: rem(16);

				@include min768 {
					font-size: rem(20);
					margin-bottom: rem(10);
				}
			}

			.ul_list {
				ul li {
					font-size: rem(16);
				}
			}
		}
	}
}

.text-title-mt {
	margin-top: rem(46);

	@include min768 {
		margin-top: rem(10);
	}
}