.page_start {
	height: 100%;
	width: 100%;
	
	.title {
		margin-top: rem(100);
		margin-bottom: rem(80);
		font-size: rem(28);
	}

	.start_list {
		li {
			margin-bottom: rem(20);
			font-size: rem(16);

			display: flex;
			align-items: center;

			span {
				margin-left: rem(10);
				font-size: rem(13);
				border-radius: rem(8);
				background-color: $green-text;
				color: white;
				padding: rem(2) rem(4);
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}