.popup {
	position: fixed;
	z-index: 200;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.4);

	display: none;
	align-items: center;
	justify-content: center;

	overflow-x: auto;

	&_wrap {
		position: relative;
		border-radius: rem(10);
		background-color: white;
		width: rem(715);
		padding: rem(62) rem(144);

		@include min768 {
			width: 100%;
			min-height: 100%;
			padding: rem(62) rem(20);
			border-radius: 0;
		}

		&-header {
			text-align: center;
			margin-bottom: rem(30);

			.title {
				font-size: rem(38);
				font-weight: 600;
				color: #373737;

				@include min768 {
					font-size: rem(28);
				}
			}
		}

		&-form {
			width: 100%;

			.form_header-two {
				margin-top: rem(34);
				margin-bottom: rem(24);
				text-align: center;

				.title {
					font-size: rem(24);
					font-weight: 600;
					color: #373737;
				}
			}

			.field-set {
				.field {
					width: 100%;

					label {
						margin-bottom: rem(6);
					}
				}
			}

			.field-control {
				justify-content: flex-start;
				margin-bottom: rem(32);
			}

			.col-3 {

				.field {
					width: 30%;
				}
			}

			.field-button {
				text-align: center;
				margin-top: rem(42);

				@include min380 {
					.button-green {
						font-size: rem(16);
					}
				}
			}
		}

		&-close {
			position: absolute;
			top: rem(16);
			right: rem(16);

			i {
				font-size: rem(24);
			}
		}
	}
}

.popup.active {
	display: flex;
}