.field-checked {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  cursor: pointer;
}

.field-checked input[type="radio"], .field-checked input[type="checkbox"] {
  display: none;
}

.field-checked input[type="radio"]:checked ~ .check::before, .field-checked input[type="checkbox"]:checked ~ .check::before {
  opacity: 1;
}

.field-checked input[type="radio"] ~ .check {
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  border: 0.0625rem solid #BFBFBF;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-right: 0.4375rem;
}

.field-checked input[type="radio"] ~ .check::before {
  content: "";
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  background-color: #38BE73;
  opacity: 0;
  transition: all 400ms ease;
}

.field-checked input[type="checkbox"] ~ .check {
  width: 1rem;
  height: 1rem;
  border: 0.0625rem solid #BFBFBF;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-right: 0.4375rem;
}

.field-checked input[type="checkbox"] ~ .check::before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background-color: #38BE73;
  opacity: 0;
  transition: all 400ms ease;
}

.field-checked .text {
  color: #373737;
}

