.section_contact {
	background-color: #F0F7FF;
	padding-bottom: rem(100);
	padding-top: rem(68);

	.text-title {
		margin-bottom: rem(22);
	}
	
	.container {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: rem(-52);
			right: rem(-88);
			width: rem(229);
			height: rem(245);
			background: url(../img/salamander.png) no-repeat;
			background-size: cover;

			@include min768 {
				top: rem(200);
				right: 0;
				width: rem(120);
				height: rem(146);
			}
		}
	}

	&-left {
		width: 42%;

		@include min {
			width: 100%;
		}

		.text {
			color: #373737;
			margin-bottom: rem(30);
			max-width: rem(380);

			@include min {
				font-size: rem(16);
				max-width: rem(420);
			}
		}

		.contact-list {
			display: flex;
			flex-direction: column;

			@include min {
				flex-wrap: wrap;
				flex-direction: row;
				margin-bottom: rem(56);
			}

			&_item {
				margin-bottom: rem(22);
				display: flex;
				align-items: center;
				color: #373737;

				@include min {
					width: 50%;
					padding-right: rem(50);

					span, a {
						font-size: rem(16);
					}
				}

				@include min768 {
					width: 100%;
					padding-right: 0;
				}

				i {
					font-size: rem(32);
					margin-right: rem(15);

					@include min {
						font-size: rem(22);
					}
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			&_item.flex-start {
				align-items: flex-start;

				a {
					line-height: 138%;

					@include min {
						font-size: rem(16);
					}
				}
			}
		}
	}

	&-right {
		width: 58%;

		padding-right: rem(100);

		@include min {
			width: 100%;
			padding-right: 0;
		}

		.button-green {
			font-size: rem(20);
		}
	}
}

.field-set {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	.field {
		width: calc(50% - 0.625rem);

		@include min768 {
			width: 100%;

			&:first-child {
				margin-bottom: rem(20);
			}
		}
	}
}

.field {
	// label {
	// 	display: block;
	// 	color: #373737;
	// 	margin-bottom: rem(7);
	// }

	input[type="text"], input[type="email"], textarea {
		// border-radius: rem(4);
		// height: rem(44);
		width: 100%;
		// border: rem(1) solid #B7C0E1;
	}
	
}

.field-set {
	textarea {
		width: 100% !important;
		height: rem(125) !important;
		max-height: rem(125);
		resize: none;
	}
}

.field-control {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	align-items: center;
}

.field-upload {
	margin-right: rem(34);

	@include min768 {
		margin-right: auto;
		margin-bottom: rem(20);
	}
	input {
		display: none;
	}

	label {
		cursor: pointer;
		display: flex;
		align-items: center;

		.paper-clip {
			width: rem(23);
			height: rem(23);
			background-size: rem(23);
			display: block;
			margin-right: rem(7);
			background: url(../img/paper-clip.svg) no-repeat;
		}
		span {
			font-size: rem(14);
			text-decoration: underline;
		}
	}
}