.speedbar {
	margin: rem(38) 0;

	@include min768 {
		margin: rem(20) 0;
	}

	a, span {
		font-size: rem(16);
		position: relative;
		margin-right: rem(30);

		@include min768 {
			font-size: rem(12);
			margin-right: rem(20);
		}
	}

	a {
		color: #373737;

		&:hover {
			text-decoration: underline;
		}

		&::after {
			content: "/";
			position: absolute;
			top: 0;
			right: rem(-15);
			font-size: rem(16);

			@include min768 {
				// right: rem(-10);
				font-size: rem(12);
			}
		}
	}

	span {
		font-weight: 600;

		&:last-of-type {
			margin-right: 0;
		}
	}

	@include min {
		span.smart {
			display: none;
		}

		a.smart {
			font-weight: 600;

			&::after {
				display: none;
			}
		}
	}
}