.blog_items {
	margin: rem(40) rem(100) 0 rem(100);

	@include min {
		margin: rem(30) 0 0 0;
	}

	.blog_item {
		margin-bottom: rem(20);
		display: flex;
		flex-wrap: wrap;
		background-color: #F0F7FF;
		border-radius: rem(10);
		overflow: hidden;

		@include min {
			height: rem(175);
		}

		@include min768 {
			height: auto;
		}

		&-left {
			flex: 0 0 rem(240);
			height: 100%;
			border-top-left-radius: rem(10);
			border-bottom-left-radius: rem(10);
			overflow: hidden;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			@include min {
				flex: 0 0 rem(168);
			}

			@include min768 {
				flex: 0 0 100%;
				height: rem(210);
				margin-bottom: rem(22);
				// border-top-left-radius: rem(10);
				border-top-right-radius: rem(10);
				border-bottom-left-radius: 0;
			}
		}

		&-right {
			padding-left: rem(25);
			padding-right: rem(25);
			flex: 1 0 0;
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			justify-content: center;

			@include min {
				padding-left: rem(20);
				padding-right: rem(20);
			}

			@include min768 {
				padding-left: rem(15);
				padding-right: rem(15);
				padding-bottom: rem(15);
			}

			.title {
				font-size: rem(24);
				font-weight: 600;
				display: block;
				color: #373737;
				margin-bottom: rem(16);

				@include min {
					font-size: rem(20);
				}

				&:hover {
					text-decoration: underline;
				}

				@include min {
					margin-bottom: rem(6);
				}
			}

			.text {
				color: #808080;

				@include min {
					font-size: rem(16);
				}
			}
		}
	}
}

.page {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: rem(50);
	margin-bottom: rem(85);

	a, span  {
		position: relative;
		width: rem(37);
		height: rem(37);
		border-radius: rem(6);
		margin-right: rem(15);
		font-size: rem(16);
		color: #808080;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px solid transparent;
		background-color: #EAF4FF;
	}

	a {
		
		&:last-child {
			margin-right: 0;
		}

		&:hover {
			border-color: #05E185;
		}
	}

	span {

		border-color: #05E185;
	}
}

.product {

	.descr {
		margin-bottom: rem(20);

		&:last-of-type {
			margin-bottom: rem(45);
		}

		&:first-of-type {
			margin-top: rem(45);

			@include min {
				margin-top: rem(36);
			}

			@include min768 {
				margin-top: rem(16);
			}
		}
	}
}

.blog_page {
	.grid_product {
		
		@include min {
			max-width: 85%;
		}

		@include min768 {
			max-width: 100%;
		}
	}

	.grid_product-left {
		margin-bottom: 0;
	}
}