.footer {
	background-color: #EAF4FF;
	padding-top: rem(29);
	padding-bottom: rem(36);

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	&_logo {
		margin-right: rem(46);

		@include min {
			order: 2;
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-right: 0;
		}

		@include min768 {
			flex-wrap: nowrap;

			.logo {
				max-width: rem(131);
			}
		}
		
		.link {
			display: none;

			@include min {
				display: block;
				font-size: rem(14);
				color: #3F3F3F;
			}

			@include min768 {
				// flex: 1 0 0;
				max-width: rem(150);
			}
		}
	}

	&_nav {
		@include min {
			order: 1;
			width: 100%;
			margin-bottom: rem(18);
		}
		ul {
			display: flex;
			flex-wrap: wrap;

			@include min {
				justify-content: space-between;
			}

			@include min768 {
				flex-direction: column;
				justify-content: flex-start;
				height: rem(105);
				
				// li {
				// 	&:nth-child(4) {
				// 		flex-grow: 1;
				// 	}
				// }
			}

			li {
				margin-right: rem(40);

				@include min {
					order: 1;
				}

				&:last-child {
					margin-right: 0;

				}

				&:first-child {
					@include min {
						display: none;
					}
				}

				a {
					font-size: rem(14);
					color: #3F3F3F;

					&:hover {
						text-decoration: underline;
					}
				}
				
			}

			@include min {
				li.last {
					margin-right: 0;
				}
			}
		}
	}
}