.section_prod {
	margin-bottom: rem(80);
	.text-title {
		margin-bottom: rem(40);
	}

	.item_prod {
		width: rem(400);
		height: rem(188);
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #F0F7FF;
		border-radius: rem(10);

		@include min {
			width: 32%;
			height: rem(145);

			img {
				height: rem(58);
			}
		}

		@include min768 {
			height: rem(80);

			img {
				height: rem(34);
			}
		}
	}
}