.grid_product {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	// grid-template-rows: 1fr auto auto;
	gap: 0px 0px;
	grid-template-areas: "left left header header"
						 "left left control control";

	@include min {
		// grid-template-columns: 1fr;
		// grid-template-rows: 100% 100% 100%;
		// grid-template-areas: "header"
		// 					 "left"
		// 					 "control";
		display: flex;
		flex-wrap: wrap;
	}

	&-left {
		position: relative;
		grid-area: left;

		@include min {
			width: 100%;
			order: 1;
			margin-bottom: rem(50);
		}

		@include min768 {
			margin-bottom: rem(20);
		}

		.slider_product-prev {
			position: absolute;
			left: 0;
			bottom: rem(22);
			i {
				font-size: rem(32);
				color: #898989;
			}

			@include min768 {
				bottom: rem(8);

				i {
					font-size: rem(20);
				}
			}
		}

		.slider_product-next {
			position: absolute;
			right: 0;
			bottom: rem(22);

			i {
				font-size: rem(32);
				color: #898989;
			}

			@include min768 {
				bottom: rem(8);

				i {
					font-size: rem(20);
				}
			}
		}

		.slider_product-top {
			width: 100%;
			margin-left: auto;
			margin-right: auto;
			height: rem(395);
			border-radius: rem(10);
			overflow: hidden;
			margin-bottom: rem(20);

			@include min768 {
				height: rem(200);
			}
		}

		.slider_product-bottom {
			height: rem(75);
			margin: 0 rem(50);
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			@include min {
				// width: 100%;
			}

			@include min768 {
				height: rem(40);
				margin: 0 rem(33);
			}

			.swiper-wrapper {
				
			}

			.swiper-slide {
				position: relative;
				border-radius: rem(10);
				overflow: hidden;
				cursor: pointer;
				display: flex;
				align-items: center;
				justify-content: center;
				// border: 3px solid;
				// border-image: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%) 21 30 30 21 repeat repeat;

				img {
					width: 100%;
					height: 100%;
					transition: all 150ms ease;
					border-radius: rem(10);
				}

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					
					border-radius: rem(10);
					background-image: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
					z-index: -1;
					opacity: 1;
					transition: all 400ms ease;
				}

				&:hover {
					&::before {
						opacity: 1;
					}

					img {
						width: calc(100% - 6px);
						height: calc(100% - 6px);
						border-radius: rem(10);
					}
				}
			}
		}

		.swiper-slide {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				// background-size: cover;
				// background-position: center;
			}
		}

	}

	&-header {
		grid-area: header;

		padding-left: rem(36);
		height: 0;

		@include min {
			height: auto;
			padding-left: 0;
			order: -9999;
		}

		.title {
			font-size: rem(38);
			font-weight: 600;
			color: #121212;
			margin-bottom: rem(30);
			display: block;

			@include min {
				font-size: rem(44);
			}

			@include min768 {
				font-size: rem(38);
				margin-bottom: rem(14);
			}
		}

		.text {
			color: #373737;
			margin-bottom: rem(40);

			@include min {
				margin-bottom: rem(18);
			}
		}
	}

	&-control {
		grid-area: control;

		padding-left: rem(36);

		@include min {
			width: 100%;
			padding-left: 0;
			order: 9999;
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
		}

		.button-b-icon {
			width: rem(315);
			height: rem(60);
			justify-content: center;

			@include min768 {
				width: 100%;
			}

			span {
				font-size: rem(20);
				font-weight: 600;
				text-transform: uppercase;
			}

			&:first-child {
				background: linear-gradient(91.33deg, #05E192 8.71%, #05E176 88.93%);
				margin-bottom: rem(17);

				@include min {
					margin-right: rem(20);
				}

				@include min768 {
					margin-right: 0;
				}

				&::after {
					display: none;
				}
			}
		}
	}


}

.table_list {
	margin-top: rem(64);
	margin-bottom: rem(50);
	padding: rem(36) rem(105);
	background-color: #F0F7FF;
	border-radius: rem(10);

	@include min {
		margin-top: rem(50);
		margin-bottom: rem(44);
		padding: rem(22) rem(25);
	}

	@include min768 {
		margin-top: rem(26);
		margin-bottom: rem(26);
		padding: rem(30) 0;
	}

	&-header {
		margin-bottom: rem(20);

		.title {
			font-size: rem(38);
			font-weight: 600;
			color: #121212;

			@include min768 {
				font-size: rem(34);
			}
		}
	}

	&-item {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-end;

		padding: rem(10) 0;

		border-bottom: rem(1) solid #CED8D4;

		@include min {
			font-size: rem(16);
		}

		@include min768 {
			align-items: flex-start;
		}

		&:last-child {
			border-bottom: 0;
		}

		b {
			font-weight: 600;
			margin-bottom: rem(8);

			@include min {
				font-size: rem(16);
			}
		}

		.left {
			display: flex;
			flex-direction: column;

			@include min768 {
				max-width: 80%;
			}
		}
		>.flex-col {
			@include min768 {
				max-width: 80%;
			}
		}
		.right {
			max-width: rem(343);
			text-align: right;

			@include min768 {
				flex: 1 0 0;
				max-width: 50%;
			}
		}
	}
}

.ul_list {
	margin-bottom: rem(32);

	@include min {
		margin-bottom: rem(20);
	}

	&-header {
		margin-bottom: rem(18);

		span {
			font-weight: 600;
			font-size: rem(24);
			color: #121212;
			display: block;

			@include min {
				font-size: rem(20);
			}
		}
	}

	.text {
		color: #373737;
		margin-bottom: rem(12);

		@include min {
			font-size: rem(16);
		}
	}

	> b {
		font-weight: 600;
		color: #373737;
		margin-bottom: rem(14);
		display: block;

		@include min {
			font-size: rem(16);
		}
	}

	ul {
		margin-bottom: rem(24);

		&:last-child {
			margin-bottom: 0;
		}
		li {
			position: relative;
			color: #373737;
			line-height: 140%;
			padding-left: rem(20);

			@include min {
				font-size: rem(16);
			}

			&::before {
				content: "-";
				position: absolute;
				top: 0;
				left: 0;
				color: $green-text;
				font-weight: 600;
			}
		}
	}
}

.product {
	.elipse {
		width: rem(580);
		height: rem(580);
		margin-top: rem(-300);

		@include min {
			width: rem(357);
			height: rem(357);
			margin-top: rem(-378);
			right: rem(40);
		}

		@include min768 {
			display: none;
		}
	}
}